<template>
  <div class="notediscussionlist">
    <v-row>
      <v-col cols="12" md="6">
        <h2 class="subheading grey--text">
          <v-icon class="mx-1">mdi-text-box-outline</v-icon>
          <span>跨領域團隊合作照會討論紀錄表</span>
        </h2>
      </v-col>
      <v-col cols="12" md="6">
        <!-- 功能表 -->
        <v-toolbar dense>
          <v-text-field
            hide-details
            prepend-icon="mdi-magnify"
            single-line
            label="搜尋"
            v-model="serachKey"
          ></v-text-field>
          <!-- <PopupAdd ref="editProcess" @editFinish="editFinish" @getAllData="getAllData" /> -->
          <PopupAdd ref="editProcess" />
        </v-toolbar>
      </v-col>
    </v-row>

    <v-card>
      <v-card-text>
        <v-data-iterator
          :items="searchfilter"
          :items-per-page.sync="itemsPerPage"
          :footer-props="{ itemsPerPageOptions }"
        >
          <template v-slot:default="props">
            <v-simple-table fixed-header class="mt-2 text-no-wrap">
              <template v-slot:default>
                <thead style="background-color: #e3f2fd">
                  <tr>
                    <th></th>
                    <th>日期</th>
                    <th>基本資料(護字號 / 姓名)</th>
                    <th>紀錄人</th>
                    <th>修改紀錄</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in props.items" :key="index">
                    <td>
                      <v-menu transition="scale-transition" offset-y>
                        <template v-slot:activator="{ on }">
                          <v-btn color="primary" dark v-on="on">功能</v-btn>
                        </template>
                        <v-list>
                          <v-list-item @click="edit(item)">
                            <v-list-item-title>修改</v-list-item-title>
                          </v-list-item>
                          <v-list-item @click="del(item)">
                            <v-list-item-title>刪除</v-list-item-title>
                          </v-list-item>
                          <v-list-item @click.stop="print(item)">
                            <v-list-item-title>列印</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>
                    </td>
                    <td>{{ item.date }}</td>
                    <td>
                      {{
                        `${item.user ? item.user.p_code : ""} / ${
                          item.user ? item.user.name : ""
                        }`
                      }}
                    </td>
                    <td>{{ item.create_man }}</td>
                    <td>{{ item.edit_man }}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </template>
        </v-data-iterator>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import PopupAdd from "@/views/main/NoteDiscussionAdd.vue";
import dayjs from "dayjs";

export default {
  components: { PopupAdd },
  data() {
    return {
      // items: [],
      serachKey: "",
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,
    };
  },

  computed: {
    items() {
      let match = this.$store.state.usingItems.map((i) => {
        let newItem = JSON.parse(i.datalist);
        newItem.snkey = i.snkey;
        return newItem;
      });
      return match.sort((a, b) => {
        return a.date < b.date ? 1 : -1;
      });
    },
    searchfilter() {
      var keys = this.serachKey.split(" ");
      var str = "";

      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
    },
  },

  mounted() {
    this.$store.dispatch("getUsingItems", "notediscussion"); //讀入本頁使用的資料
    // this.getAllData();
  },

  methods: {
    //點選進入修改功能 -> 傳資料到修改畫面
    edit(item) {
      this.$refs.editProcess.$emit("fromParent", item);
    },
    //刪除功能
    del(delData) {
      this.$confirm("確認刪除? 包含的照片也會一併刪除!!").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: "notediscussion",
          info: JSON.stringify(delData),
        };

        if (res) {
          //有含照片時要先刪除照片
          if (delData.picName.length) {
            let url =
              "general/delPicMulti/" +
              this.$store.state.databaseName +
              "/notediscussion/";
            let postPicName = {
              picNames: delData.picName,
            };
            this.$api.options(url, postPicName).then((rs) => {
              // console.log(rs)
            });
          }

          //刪除資料本體
          this.$api
            .options(
              "general/delv3/" +
                this.$store.state.databaseName +
                "/notediscussion",
              postData
            )
            .then((rs) => {
              if (rs.state == 1) {
                var pop = { msg: "已刪除", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.$store.dispatch("getUsingItems", "notediscussion"); //讀入本頁使用的資料
                // this.getAllData();
              }
            });
        }
      });
    },
    //列印功能
    print(item) {
      let routeData = this.$router.resolve({
        path: "/print/notediscussionprint",
        query: { data: JSON.stringify(item) },
      });
      window.open(routeData.href, "_blank");
    },
  },
};
</script>

<style lang="scss">
.notediscussionlist {
  h2 {
    span {
      vertical-align: middle;
    }
  }
  thead {
    th {
      font-size: 1.2rem;
      font-family: "微軟正黑體";
      color: red;
    }
  }
  tbody {
    td {
      font-size: 1rem;
      border-bottom: none;
    }
  }
}
</style>