<template>
  <div>
    <div class="mb-2">
      <v-btn class="success ml-3" @click="$refs.fileInput.click()">
        上傳照片<v-icon>mdi-cloud-upload</v-icon>
      </v-btn>
      <!-- 上傳file用的input -->
      <input
        type="file"
        multiple
        accept="image/*"
        ref="fileInput"
        @change="onFileSelected"
        style="display: none"
      />
    </div>

    <!-- 圖片預覽區 -->
    <v-alert color="error" outlined v-if="previewPics && previewPics.length > 0">
    <v-chip class="error">未上傳圖片</v-chip> 
    <v-row>
        <v-col cols="4" v-for="(pic,index) in previewPics" :key="index">
        <v-img :src="pic.picUrl" class="text-right pa-1" max-width="400">
            <v-icon class="red pa-2 white--text" @click.stop="delPreviewPics(index)">mdi-close</v-icon>
        </v-img>
        </v-col>
    </v-row>
    </v-alert>
    
    <!-- 已上傳圖片顯示區 -->
    <v-alert
      color="primary"
      outlined
      v-if="uploadedImages && uploadedImages.length > 0"
    >
      <v-chip class="primary">已上傳圖片</v-chip>
      <v-row>
        <v-col cols="4" v-for="(pic, index) in uploadedImages" :key="index">
          <v-img
            :src="$store.state.base_url + 'upload/'+ directory +'/' + pic"
            :lazy-src="
              $store.state.base_url + 'upload/'+ directory +'/lazypic.jpg'
            "
            class="text-right pa-1"
            max-width="400"
          >
            <v-icon
              class="red pa-2 white--text"
              @click.stop="delPic(pic, index)"
              >mdi-close</v-icon
            >
          </v-img>
        </v-col>
      </v-row>
    </v-alert>
    <!-- <pre>
      unUplodFiles:{{unUplodFiles}}
    </pre> -->
  </div>
</template>

<script>
export default {
  /*
  uploadedImages(傳過來的已上傳圖片) 
  directory(圖片存取的目錄) 
  dialog(判斷開啟時要清空previewPics: [], //未上傳前預覽照片)
  */
  props: ["uploadedImages" , "directory" , "dialog"],
  data() {
    return {
      selectedFiles: "", //本次選擇檔案暫存
      unUplodFiles: [], //未上傳前檔案暫存
      previewPics: [], //未上傳前預覽照片
    };
  },

  watch: {
    dialog(newVal){
      if (newVal){
        this.previewPics = [];
        this.unUplodFiles = [];
      }
    },
    unUplodFiles(newVal) {
      this.previewPics = [];
      newVal.forEach((item) => {
        let reader = new FileReader();
        reader.readAsDataURL(item);
        reader.onload = (e) => {
          this.previewPics.push({ picUrl: e.target.result });
        };
      });
    },
  },

  methods: {
    //選取圖片
    onFileSelected(event) {
      this.selectedFiles = event.target.files;
      if ((this.uploadedImages.length + this.selectedFiles.length + this.previewPics.length) > 3){
        var pop = { msg: "照片數量不可超過3張!!", type: true, theme: "warning" }; this.$store.commit("snackbar", pop);
        return false;
      } 

      var fitType = ["gif", "png", "jpg", "jpeg"]; //符合檔案類型
      var errorMsg = ""; //判斷錯誤訊息
      this.selectedFiles.forEach((item) => {
        let okFlag = true;
        var extension = item.name.split(".").pop().toLowerCase(); //取副檔名
        if (fitType.indexOf(extension) == -1) {
          errorMsg += "檔案:" + item.name + "不是支持的影像檔案";
          okFlag = false;
        }
        if (item.size > 1024 * 1024 * 5) {
          //檔案大小設定-最大5MB
          errorMsg += "檔案:" + item.name + "不能超過5M唷";
          okFlag = false;
        }

        //加入判斷同檔名不加入的設定
        let matchFile = this.unUplodFiles.find((unUplodItem) => {
          return item.name == unUplodItem.name;
        });
        if (matchFile) {
          errorMsg += "檔案:" + item.name + "同檔名不可重覆上傳";
          okFlag = false;
        }

        if (okFlag) {
          this.unUplodFiles.push(item);
        }
      });

      if (errorMsg == "") {
        this.$emit("changeUnUploadFiles", this.unUplodFiles);
      }else{  
        var pop = { msg: errorMsg, type: true, theme: "warning" };
        this.$store.commit("snackbar", pop);
        this.selectedFiles = "";
      }
      this.$refs.fileInput.value = null;  //加這行才可以重覆上傳同一個檔案;有時會刪掉再重傳一次時用到
    },
    //刪除暫存的圖片
    delPreviewPics(index){
      this.previewPics.splice(index,1)
      this.unUplodFiles.splice(index,1)
    },
    //刪除伺服器上的圖片
    delPic(item, index) {
      this.$confirm("確認刪除?").then((res) => {
        if (res) {
          //刪除資料
          this.axios
            .post(
              "general/delPic/" +
                this.$store.state.databaseName +
                "/"+ this.directory +"/"+ this.directory +"/" +
                item
            )
            .then((rs) => {
              var pop = { msg: "已刪除", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.uploadedImages.splice(index, 1);
              //刪掉照片時要更動原始資料記錄的圖檔資料;傳true是區分單刪除圖片時,不關掉dailog的動作
              this.$emit("editContentToData", true);
            });
        }
      });
    },
  },
};
</script>

