<template>
  <div class="notediscussionadd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{
          title
        }}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
            <v-simple-table>
              <tr>
                <td style="width: 25%">
                  <span class="subTitle">討論時間</span>
                </td>
                <td>
                  <v-text-field
                    label="日期"
                    v-model="list.date"
                    type="date"
                    :rules="emptyRules"
                  ></v-text-field>
                  <div class="d-flex align-center">
                    <v-text-field
                      label="時間(起)"
                      v-model="list.timeStart"
                      type="time"
                    ></v-text-field>
                    <span class="mx-2">～</span>
                    <v-text-field
                      label="時間(迄)"
                      v-model="list.timeEnd"
                      type="time"
                    ></v-text-field>
                  </div>
                </td>
                <td>
                  <v-text-field
                    label="主席"
                    v-model="list.chairman"
                    v-if="dialog"
                    autofocus
                  ></v-text-field>
                  <v-text-field
                    label="紀錄"
                    v-model="list.recorder"
                  ></v-text-field>
                </td>
              </tr>

              <tr>
                <td>
                  <span class="subTitle">團隊成員</span>
                </td>
                <td colspan="2" class="pb-3">
                  <div
                    class="d-flex"
                    v-for="(teamMember, index) in teamMembers"
                    :key="index"
                  >
                    <v-checkbox
                      readonly
                      :label="teamMember.label"
                      class="mr-2"
                      v-model="list[teamMember.columnName]"
                      hide-details
                    ></v-checkbox>
                    <v-text-field
                      v-model="list[teamMember.columnName]"
                      hide-details
                      clearable
                    ></v-text-field>
                  </div>
                </td>
              </tr>

              <tr>
                <td>
                  <span class="subTitle">討論議題</span>
                </td>
                <td colspan="2" class="pa-3">
                  <v-textarea
                    v-model="list.discussionTopics"
                    solo
                    hide-details
                  ></v-textarea>
                </td>
              </tr>

              <tr>
                <td class="pa-3">
                  <span class="subTitle d-block mb-4">基本資料</span>
                  <v-autocomplete
                    @change="
                      getNursingassessment();
                      getTreatment();
                    "
                    :items="userSelectItems"
                    label="住民選擇(護字號 / 姓名)"
                    clearable
                    return-object
                    v-model="list.user"
                    :rules="emptyRules"
                  ></v-autocomplete>
                </td>
                <td colspan="2" class="pa-3">
                  <v-row v-if="this.list.user" style="font-size: 1.2rem">
                    <v-col cols="3">姓名: {{ this.list.user.name }} </v-col>
                    <v-col cols="3">性別: {{ this.list.user.sex }}</v-col>
                    <v-col cols="3">床號: {{ this.list.user.bed_name }}</v-col>
                    <v-col cols="3"
                      >年齡: {{ showAge(this.list.user.birthday) }}</v-col
                    >

                    <v-col cols="4"
                      >入住日期: {{ this.list.user.in_date }}</v-col
                    >

                    <v-col cols="4"
                      >學歷:
                      <span v-if="nursingassessment">{{
                        showEducation()
                      }}</span></v-col
                    >
                    <v-col cols="4"
                      >婚姻狀況: {{ showMarry(this.list.user) }}</v-col
                    >

                    <v-col cols="4"
                      >入住來源: {{ this.list.user.source }}</v-col
                    >
                    <v-col cols="8"
                      >入住診斷: {{ showDiagnosis(this.list.user) }}
                    </v-col>
                    <v-col cols="12"
                      >經濟狀況:
                      <span v-if="treatment">{{
                        this.treatment.economicsources
                      }}</span>
                    </v-col>
                    <v-col cols="12"
                      >家系圖:
                      <v-img
                        :src="
                          $store.state.base_url +
                          'upload/familytree/' +
                          list.user.pic_url_familytree
                        "
                        :lazy-src="
                          $store.state.base_url +
                          'upload/familytree/lazypic.jpg'
                        "
                        class="grey lighten-2"
                        height="500px"
                        contain
                        v-if="list.user.pic_url_familytree != 'lazypic.jpg'"
                      ></v-img>
                    </v-col>
                  </v-row>
                </td>
              </tr>

              <tr>
                <td colspan="3" class="text-center">
                  <span class="subTitle">討論內容</span>
                </td>
              </tr>

              <tr>
                <td>
                  <span class="subTitle">病史及入住過程</span>
                </td>
                <td colspan="2" class="pa-3">
                  <v-textarea
                    v-model="list.historyAndProcess"
                    solo
                    hide-details
                  ></v-textarea>
                </td>
              </tr>

              <tr>
                <td>
                  <span class="subTitle">目前情形</span>
                </td>
                <td colspan="2" class="pa-3">
                  <v-text-field
                    v-model="list.currentSituation"
                    hide-details
                  ></v-text-field>
                </td>
              </tr>

              <tr>
                <td>
                  <span class="subTitle">管路情形</span>
                </td>
                <td colspan="2" class="pa-3">
                  <v-text-field
                    v-model="list.pipelineSituation"
                    hide-details
                  ></v-text-field>
                </td>
              </tr>

              <tr>
                <td>
                  <span class="subTitle">協助照顧執行目標</span>
                </td>
                <td colspan="2" class="pa-3">
                  <v-text-field
                    v-model="list.careGoals"
                    hide-details
                  ></v-text-field>
                </td>
              </tr>

              <tr>
                <td>
                  <span class="subTitle">社會互動情形</span>
                </td>
                <td colspan="2" class="pa-3">
                  <v-textarea
                    v-model="list.socialInteraction"
                    hide-details
                    solo
                  ></v-textarea>
                </td>
              </tr>

              <tr>
                <td>
                  <span class="subTitle">護理問題</span>
                </td>
                <td colspan="2" class="pa-3">
                  <v-textarea
                    v-model="list.nursingProblem"
                    hide-details
                    solo
                  ></v-textarea>
                </td>
              </tr>

              <tr>
                <td>
                  <span class="subTitle">建議措施</span>
                </td>
                <td colspan="2" class="pa-3">
                  <v-textarea
                    v-model="list.suggested"
                    hide-details
                    solo
                  ></v-textarea>
                </td>
              </tr>

              <tr>
                <td>
                  <span class="subTitle">跨專業討論</span>
                </td>
                <td colspan="2" class="pa-3">
                  <v-textarea
                    v-model="list.interdisciplinaryDiscussion"
                    hide-details
                    solo
                  ></v-textarea>
                </td>
              </tr>

              <tr>
                <td>
                  <span class="subTitle">評值及追蹤</span>
                </td>
                <td colspan="2" class="pa-3">
                  <v-textarea
                    v-model="list.evaluationTracking"
                    hide-details
                    solo
                  ></v-textarea>
                </td>
              </tr>

              <tr>
                <td>
                  <span class="subTitle">結論</span>
                </td>
                <td colspan="2" class="pa-3">
                  <v-textarea
                    v-model="list.conclusion"
                    hide-details
                    solo
                  ></v-textarea>
                </td>
              </tr>

              <tr>
                <td>
                  <span class="subTitle"> 照片(最多3張) </span>
                </td>
                <td colspan="2" class="pa-3">
                  <UploadImages
                    directory="notediscussion"
                    :dialog="dialog"
                    :uploadedImages="this.list.picName"
                    @editContentToData="editContentToData"
                    @changeUnUploadFiles="changeUnUploadFiles"
                  />
                </td>
              </tr>

              <!-- <pre>
          unUplodFiles:{{unUplodFiles}}
        </pre> -->
            </v-simple-table>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="primary"
            @click="addOK"
            v-if="processType == 'add'"
            :loading="loading"
            :disabled="loading"
            >確認新增</v-btn
          >
          <v-btn
            class="success"
            @click="editOK"
            v-if="processType == 'edit'"
            :loading="loading"
            :disabled="loading"
            >確認修改</v-btn
          >
        </v-card-actions>
        <!-- <pre>

        {{list}}
        </pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";
//2021.4.16 自已設計的多筆上新增刪除圖片用
import UploadImages from "@/components/UploadImages.vue";

export default {
  components: { UploadImages },
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
      loading: false,
      //團隊成員
      teamMembers: [
        { label: "醫生", columnName: "doctor" },
        { label: "居家護理師", columnName: "homeNurse" },
        { label: "護理人員", columnName: "nursing" },
        { label: "營養師", columnName: "nutritionist" },
        { label: "社工員", columnName: "socialWorker" },
        { label: "物理治療師", columnName: "physicalTherapist" },
        { label: "藥師", columnName: "pharmacist" },
        { label: "照服員", columnName: "attendant" },
        { label: "家屬", columnName: "familyMembers" },
        { label: "其他", columnName: "otherMember" },
      ],
      userItems: [],
      nursingassessment: null,
      treatment: null,
      unUplodFiles: [], //未上傳前檔案暫存
    };
  },

  computed: {
    userSelectItems() {
      return this.userItems.map((i) => {
        let newItem = { ...i };
        newItem.text = `${i.p_code} / ${i.name}`;
        return newItem;
      });
    },
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });
    this.getUser();
  },

  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = {
        date: dayjs().format("YYYY-MM-DD"),
        picName: [],
      };
      this.previewPics = [];
      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.list = { ...item }; //複制一份實際修改資料內容
    },
    //確認新增
    addOK() {
      //判斷是否需要上傳圖片(unUplodFiles裡沒有東西就是不用上傳圖檔)
      if (this.unUplodFiles.length && this.$refs.form.validate()) {
        console.log("run upload pic");
        let uploadRS = this.onUpload(); //拿到上傳後的檔案名
        uploadRS.then((rs) => {
          rs.forEach((item) => {
            this.list.picName.push(item);
          });
          this.addContentToData();
        });
      } else {
        console.log("run add list");
        this.addContentToData();
      }
    },
    //將資料寫入資料庫(不含圖片,圖片另外有上傳功能upload處理)
    addContentToData() {
      if (this.$refs.form.validate()) {
        this.list.create_man =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")"; //執行動作的人,時間,內容

        let postData = {
          datalist: JSON.stringify(this.list),
        };

        this.loading = true;
        this.$api.options(
          "general/add/" + this.$store.state.databaseName + "/notediscussion",
          postData
        ).then((rs) => {
          if (rs.state == 1) {
            var pop = { msg: "已新增", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            // this.$emit("getAllData");
            this.$store.dispatch("getUsingItems", "notediscussion");
            this.loading = false;
            this.dialog = false;
            // this.previewPics = [];
          } else {
            alert("error");
          }
        });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //判斷是否需要上傳圖片(unUplodFiles裡沒有東西就是不用上傳圖檔)
      if (this.unUplodFiles.length && this.$refs.form.validate()) {
        console.log("run upload pic");
        let uploadRS = this.onUpload(); //拿到上傳後的檔案名(Promise形態)
        uploadRS.then((rs) => {
          rs.forEach((item) => {
            this.list.picName.push(item);
          });
          this.editContentToData();
        });
      } else {
        console.log("run edit list");
        this.editContentToData();
      }
    },
    //確認修改
    editContentToData(delPic = false) {
      if (this.$refs.form.validate()) {
        this.loading = true;
        if (this.list.edit_man == undefined) {
          this.list.edit_man = "";
        }
        this.list.edit_man =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")" +
          this.list.edit_man; //執行動作的人,時間,內容

        let postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
          edit_man: "", //伺服端要有這個不然不能改.^^
        };

        this.$api.options(
          "general/edit/" + this.$store.state.databaseName + "/notediscussion",
          postData
        ).then((rs) => {
          if (rs.state == 1) {
            var pop = { msg: "已修改", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.$store.dispatch("getUsingItems", "notediscussion");
            if (!delPic) {
              this.dialog = false;
            } //delPic時只有單純修改內容,不關掉dailog
            this.loading = false;
          }
        });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    //取得選取的病歷資料
    getUser() {
      //取得所有資料
      this.$api.options("user/getAll/" + this.$store.state.databaseName).then(
        (rs) => {
          var getData = rs.map(function (obj) {
            if (obj.pic_url == "") {
              obj.pic_url = "lazypic.jpg";
            }
            if (obj.pic_url_familytree == "") {
              obj.pic_url_familytree = "lazypic.jpg";
            }
            return obj;
          });
          this.userItems = getData;
        }
      );
    },
    //取得入院護理評估資料
    getNursingassessment() {
      if (!this.list.user) {
        return false;
      }
      var postData = {
        key: "u_snkey",
        value: this.list.user.snkey,
      };

      this.$api.options(
        "general/getByKey/" +
          this.$store.state.databaseName +
          "/nursingassessment",
        postData
      ).then((rs) => {
        if (rs.length) {
          this.nursingassessment = JSON.parse(rs[0].datalist);
        }
      });
    },
    //取得個案服務處遇計畫表
    getTreatment() {
      if (!this.list.user) {
        return false;
      }
      var postData = {
        key: "u_snkey",
        value: this.list.user.snkey,
      };

      this.$api.options(
        "general/getByKey/" + this.$store.state.databaseName + "/treatment",
        postData
      ).then((rs) => {
        if (rs.length) {
          this.treatment = JSON.parse(rs[0].datalist);
        } else {
          this.treatment = null;
        }
      });
    },
    //回傳年齡
    showAge(birthday) {
      if (birthday == "0000-00-00") {
        return "未設定生日";
      } else {
        return dayjs().format("YYYY") - dayjs(birthday).format("YYYY");
      }
    },
    //回傳婚姻狀況
    showMarry(user) {
      if (user.marry1) {
        return "未婚";
      }
      if (user.marry2) {
        return "已婚";
      }
      if (user.marry3) {
        return "離婚";
      }
      if (user.marry4) {
        return "喪偶";
      }
      if (user.marry5) {
        return "分居";
      }
      if (user.marry6) {
        return "其他:" + user.marry7;
      }
    },
    //回傳教育程度
    showEducation() {
      if (this.nursingassessment.education1) {
        return "不識字";
      }
      if (this.nursingassessment.education2) {
        return "國小以下";
      }
      if (this.nursingassessment.education3) {
        return "小學";
      }
      if (this.nursingassessment.education4) {
        return "國中";
      }
      if (this.nursingassessment.education5) {
        return "高中(職)";
      }
      if (this.nursingassessment.education6) {
        return "大學/大專";
      }
      if (this.nursingassessment.education7) {
        return "大學以上";
      }
      if (this.nursingassessment.education8) {
        return "不確定";
      }
    },
    //回傳入住診斷
    showDiagnosis(user) {
      // console.log('user',user.diagnosis)
      let newObj = Object.values(user.diagnosis);
      // console.log('newObj',newObj)
      return newObj.reduce((str, item, index) => {
        return (str += `(${index + 1})${item} `);
      }, "");
    },
    //更新未上傳圖片或檔案
    changeUnUploadFiles(items) {
      this.unUplodFiles = items;
    },
    //上傳圖片(多檔)
    onUpload() {
      var fd = new FormData();
      this.unUplodFiles.forEach((item) => {
        fd.append("files[]", item);
      });
      fd.append("snkey", "");

      let postData = {
        url:
          "general/fileUploadMulti/" +
          this.$store.state.databaseName +
          "/notediscussion/notediscussion",
        fd: fd,
      };
      return this.$store.dispatch("upLoad", postData); //上傳圖檔及將新建立的圖檔名回傳
    },
    
  },
};
</script>

<style lang="scss">
.subTitle {
  font-size: 1.5rem;
}
td {
  border-bottom: 1px solid #000;
}
</style>
